import * as _ from "ramda";
import { FilterOption } from "@/typings/asideFilter";

export const watchFilterOptions = (options: FilterOption[]) => {
  return _.flatten(
    options.map(option => {
      return option.items.filter(item => !!item.value).map(item => item.label);
    })
  );
};

export const closeTag = (options: FilterOption[], tag: string) => {
  return options.map(option => {
    return {
      ...option,
      items: option.items.map(item => {
        if (item.label === tag) {
          return {
            label: item.label,
            value: false
          };
        }
        return item;
      })
    };
  });
};

export const closeAllTags = (options: FilterOption[]) => {
  return options.map(option => {
    return {
      ...option,
      items: option.items.map(item => ({
        label: item.label,
        value: false
      }))
    };
  });
};
